@media screen and (max-width: 576px) {
  .not-found-container {
    width: 50%;
    height: 200px;
    border-radius: 12px;
  box-shadow: 10px 10px 50px 5px #eb0404;
  }
  
  .not-found-image {
    width: 100%;
    height: 200px;
    border-radius: 12px;
  }
}

@media screen and (min-width: 577px) {
  .not-found-container {
    width: 40%;
    height: 400px;
    border-radius: 12px;
    box-shadow: 10px 10px 50px 5px #eb0404;
  }
  
  .not-found-image {
    width: 100%;
    height: 100%;
    border-radius: 12px;
  }
}

